body {
  font-family: Poppins, "Segoe UI", Roboto, sans-serif;
}

#about {
  background-color: #ffffff; /* Change background to white */
  color: #1e293b; /* Dark text for contrast */
  padding: 4rem 1.5rem; /* py-16 px-6 */
  font-family: inherit; /* Use the common font */
}

@media (min-width: 768px) {
  #about {
    padding: 4rem 5rem; /* md:px-20 */
  }
}

#about h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700; /* font-bold */
  color: #000000; /* Change to black */
  margin-bottom: 1rem; /* mb-4 */
  text-align: center; /* Center the heading */
}

#about p {
  font-size: 1.125rem; /* text-lg */
  line-height: 1.75; /* leading-relaxed */
  color: #4b5563; /* Neutral text color */
  margin-top: 1rem; /* mt-4 */
}

#about span {
  font-weight: 600; /* font-semibold */
}

#about span.text-white {
  color: #1e293b; /* Dark text for contrast */
}

#about span.text-cyan-300 {
  color: #22d3ee; /* Cyan for highlights */
}

#education-certifications {
  background-color: #1e293b; /* bg-slate-900 */
  color: #ffffff; /* text-white */
  padding: 4rem 1.5rem; /* py-16 px-6 */
  font-family: Poppins, "Segoe UI", Roboto, sans-serif;
}

@media (min-width: 768px) {
  #education-certifications {
    padding: 4rem 5rem; /* md:px-20 */
  }
}

#education-certifications h2 {
  font-size: 1.875rem; /* text-3xl */
  font-weight: 700; /* font-bold */
  color: #000000; /* Changed to black */
  margin-bottom: 1rem; /* mb-4 */
  text-align: center; /* Center the heading */
}

#education-certifications p,
#education-certifications ul {
  font-size: 1.125rem; /* text-lg */
  line-height: 1.75; /* leading-relaxed */
  color: #94a3b8; /* text-slate-300 */
}

#education-certifications ul {
  list-style-type: disc;
  padding-left: 1.25rem; /* pl-5 */
}

#education-certifications li {
  margin-bottom: 0.5rem;
}

.text-accent {
  color: #1677ff; 
}
