.hero-metrics {
  padding: 40px 0;
  background-color: #ffffff; /* Change background to white */
  font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif; /* Explicitly set font family */
}

.metrics-container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.metrics-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #000000; /* Changed to black for consistency */
  margin-bottom: 30px;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif;
  text-align: center; /* Explicitly set text alignment */
}

.metric-card {
  text-align: center;
  background-color: #f9fafb; /* Light gray for subtle contrast */
  color: #4b5563; /* Neutral text color */
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  height: 100%;
  transition: transform 0.3s ease;
}

.metric-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px rgba(22, 119, 255, 0.1); /* Secondary color shadow */
}

.metric-icon {
  font-size: 2.5rem;
  color: #1677ff; /* Secondary color for icons */
  margin-bottom: 15px;
}

.metric-title {
  font-size: 1.4rem !important; /* Standardized font size */
  font-weight: bold;
  color: #1e293b; /* Dark text for contrast */
  margin-bottom: 10px !important;
  line-height: 1.4;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif; /* Add explicit font */
}

.metric-description {
  font-size: 1rem; /* Standardized font size for description */
  color: #6b7280; /* Neutral text color */
  font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif; /* Add explicit font */
  line-height: 1.5;
}

/* Add a border bottom with secondary color on hover */
.metric-card:hover .metric-title {
  color: #1677ff; /* Secondary color on hover */
}

/* Removed cert-specific styles as we're using consistent styling now */
