.projects-section {
  background-color: #f3f4f6; /* Slightly darker background */
  color: #1e293b; /* Dark text for contrast */
  padding: 40px 0;
  font-family: Poppins, "Segoe UI", Roboto, sans-serif;
}

.projects-title {
  font-size: 2rem;
  font-weight: bold;
  color: #000000; /* Black text for headings */
  margin-bottom: 24px;
  text-align: center;
}

.project-card {
  background-color: #ffffff; /* White for project cards */
  color: #4b5563; /* Neutral text color */
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.project-card:hover {
  background-color: #f3f4f6; /* Slightly darker white on hover */
}

.project-card h4 {
  color: #1677ff; /* Secondary color for consistency */
  font-family: Poppins, "Segoe UI", Roboto, sans-serif;
}

.project-card p {
  font-size: 1rem; /* Consistent paragraph font size */
  font-family: Poppins, "Segoe UI", Roboto, sans-serif;
}
