.footer {
  background-color: #f9fafb; /* Lighter background */
  color: #d9d9d9;
  text-align: center;
  padding: 20px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer p {
  margin: 0;
  font-size: 14px;
}

.footer p span {
  color: #40a9ff;
}

.footer .footer-built {
  margin-top: 10px;
  font-size: 12px;
  color: #a6a6a6;
}
