/* add all css here */

.contact-section {
  padding: 40px 0;
  background-color: #ffffff; /* Change background to white */
  font-family: Poppins, "Segoe UI", Roboto, sans-serif; /* Updated font-family */
}

.contact-container {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.contact-title {
  font-size: 2.5rem; /* Updated size for consistency */
  font-weight: bold;
  color: #000000; /* Changed to black */
  margin-bottom: 24px;
  text-align: center; /* Ensure heading is centered */
}

.contact-description {
  font-size: 1rem; /* text-lg */
  color: #4b5563; /* Neutral text color */
  margin-bottom: 16px;
}

.contact-button {
  background-color: #1677ff; /* Updated to match secondary color */
  border-color: #1677ff; /* Updated to match secondary color */
  color: #fff;
  font-weight: bold;
  margin-bottom: 24px;
}

.contact-button:hover {
  background-color: #0958d9; /* Darker blue for hover */
  border-color: #0958d9;
}

.contact-icons a {
  color: #4b5563; /* Neutral text color */
  transition: color 0.3s;
}

.contact-icons a:hover {
  color: #1677ff; /* Updated to match secondary color */
}

.contact-details {
  margin-top: 24px;
  font-size: 1rem; /* text-lg */
  color: #94a3b8; /* text-slate-400 */
}

.contact-details .ant-space {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-details div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem; /* space-x-4 */
  margin-bottom: 1rem; /* mb-4 */
}

.contact-details div:last-child {
  margin-bottom: 0; /* Remove margin for the last item */
}

.contact-info-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
  width: 100%;
  padding: 0 20px;
}

.contact-icons {
  display: flex;
  align-items: center;
  gap: 24px;
}

.contact-icons a {
  color: #4b5563;
  transition: color 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-icons a:hover {
  color: #1677ff;
}

.contact-details {
  display: flex;
  align-items: center;
  gap: 24px;
  margin-top: 0;
}

.contact-detail-item {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 0 !important;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .contact-info-container {
    flex-direction: column;
    gap: 24px;
  }
  
  .contact-details {
    flex-direction: column;
  }
}

