.email-invitation {
  display: block;
  margin: 32px auto 40px;
  text-decoration: none;
  width: fit-content;
}

.email-card {
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: white;
  padding: 14px 24px;
  border-radius: 50px;
  box-shadow: 0 4px 15px rgba(22, 119, 255, 0.15);
  transition: all 0.3s ease;
  border: 2px solid #1677ff;
}

.email-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #1677ff;
  transition: all 0.3s ease;
  transform: translateX(0);
}

.email-text {
  font-weight: 600;
  color: #1677ff;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.email-card:hover {
  background-color: #1677ff;
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(22, 119, 255, 0.25);
}

.email-card:hover .email-icon {
  color: white;
  transform: translateX(4px);
  animation: float 1s ease-in-out infinite;
}

.email-card:hover .email-text {
  color: white;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) translateX(0);
  }
  50% {
    transform: translateY(-5px) translateX(5px);
  }
}

