@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

:root {
  --primary-color: #000;  /* Add your desired color value */
  --heading-color: #1f2937;
  --heading-color-dark-bg: #ffffff;
  --accent-color: #1677ff;  /* Professional light blue that works on dark backgrounds */
  --accent-color-light-bg: #083988;  /* Darker version for light backgrounds */
}

/* Global styles */
body {
  background-color: #f7f9fc;
  color: #1f2937;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  color: var(--heading-color);
  font-weight: 600;
}

.dark-bg h1, .dark-bg h2, .dark-bg h3,
section[class*="bg-slate"] h1, 
section[class*="bg-slate"] h2, 
section[class*="bg-slate"] h3 {
  color: var(--heading-color-dark-bg);
}

/* New accent text classes */
.text-accent {
  color: var(--accent-color);
}

.bg-light .text-accent {
  color: var(--accent-color-light-bg);
}

label {
  color: #4b5563;
}

a {
  color: #154a7d;
  text-decoration: none;
}

a:hover {
  color: #40a9ff;
}
