.custom-navbar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    height: 64px;
    padding: 0 20px;
    background: rgba(24, 144, 255, 0.7); /* light transparent */
    backdrop-filter: blur(12px); /* glass effect */
    -webkit-backdrop-filter: blur(12px);
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
    font-family: 'Poppins', 'Segoe UI', 'Roboto', sans-serif;
  }
  
  .glass-effect {
    background: rgba(24, 144, 255, 0.75);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }
  
  .navbar-content {
    max-width: 80%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .menu-button {
    font-size: 22px;
    color: white;
    background: transparent;
    border: none;
  }
  
  .navbar-desktop-links {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  
  .menu {
    background: transparent !important;
    border-bottom: none !important;
    font-weight: 600;
  }
  
  .menu .ant-menu-item {
    border-radius: 18px;
  }
  
  .menu .ant-menu-item a {
    color: white;
    font-size: 16px;
  }
  
  .menu .ant-menu-item:hover {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 18px;
  }
  
  .menu .ant-menu-item-active,
  .menu .ant-menu-item-selected {
    border-radius: 18px !important;
  }

  /* Add button click effect classes */
  .menu .ant-menu-item.clicked {
    background: rgba(255, 255, 255, 0.25);
    transition: background 0.5s ease-out;
  }
  
  .cv-button.clicked {
    background: #40a9ff;
    transition: background 0.5s ease-out;
  }
  
  .logo-button.clicked {
    background: rgba(255, 255, 255, 0.25) !important;
    transition: background 0.5s ease-out;
  }
  
  .cv-button {
    border-radius: 18px;
    padding: 6px 18px;
    font-weight: bold;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2);
  }

  .logo-button {
    font-size: 24px !important;
    font-weight: 700 !important;
    color: white !important;
    padding: 0 12px !important;
    height: auto !important;
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
    transition: transform 0.3s, color 0.3s !important;
  }
  
  .logo-button:hover {
    transform: scale(1.05);
    background: rgba(255, 255, 255, 0.1) !important;
    border-radius: 18px !important;
  }
  
  .logo-button:active {
    transform: scale(0.95);
  }
  
  .logo-dot {
    color: #e0f7ff;
  }

/* Ensure menu items don't collapse */
.navbar-desktop-links .menu {
  flex-wrap: nowrap;
  overflow-x: visible;
  min-width: fit-content;
}

/* Adjust content width to give more space for menu */
@media (max-width: 991px) and (min-width: 768px) {
  .navbar-content {
    max-width: 95%;
  }
  
  .navbar-desktop-links {
    gap: 15px;
  }
  
  .menu .ant-menu-item a {
    font-size: 14px;
  }
}
